<template>
  <div class="content" id="s-map">
    <!-- ID1920PX1080PX -->    
    <div id="myEchart" style="width:1320px; height:800px;"></div>
  </div>
</template>
 
<script>
//import $ from "jquery";/* JQUERY$.get */
var echarts = require("echarts");/* echarts */
 
export default {
  data() {
    return {
      myChart: undefined,/*  */
    };
  },
  mounted() {
  var smap = document.getElementById('s-map');
  var  chartMap = document.getElementById('myEchart');
  var resizeWorldMapContainer = function () {
               chartMap.style.width  = window.innerWidth +'px';
               chartMap.style.height = window.innerHeight-600 + 'px';
           };
                var myChart = echarts.init(chartMap);  /*  */
/*------from  $.get(....)   to $.ajax(......)   -liuzhiping
    $.get('../static/world.json', function (geoJson) {/* json 
        echarts.registerMap('world1', geoJson);/* world 
        myChart.setOption({/* myChart 
        tooltip: {
          trigger: 'item',
          formatter: '{b}'
        },
        series: [
          {
            type: 'map',
            mapType: 'world1', // 
            itemStyle: {
             // normal: { label: { show: true } }, // 
              emphasis: { label: { show: true } } // 
            },
            roam: true, // 
          }
        ]
      })
      resizeWorldMapContainer();
      console.log("aaaaa----"+smap.clientWidth);
    
     })

     */
     $.ajax({ 
    type:"get",    //
    url: "../static/world.json", 
    dataType:"json",//
    async: false,    //
    data:{       },      //datapost,
    success:function(data){      //
      echarts.registerMap('world1', data);/* world */
      myChart.setOption({/* myChart */
       tooltip: {
       trigger: 'item',
       formatter: '{b}'
         },
        series: [
        {
        type: 'map',
        mapType: 'world1', // 
        itemStyle: {
        // normal: { label: { show: true } }, // 
         emphasis: { label: { show: true } } // 
         },
         roam: true, // 
          }
                  ]
    });
  },
     error:function(result) { 
            console.log("");
         }
     });

     window.addEventListener("resize", function(){
             resizeWorldMapContainer();
             console.log("asdfsdfsdf");
             myChart.resize();
             })
  }
   
}
</script>


