import Vue from 'vue'
import Router from 'vue-router'
import App11 from '@/App1-1'
import App1 from '@/App1'
import App3 from '@/App3'
Vue.use(Router)

export default new Router({
  //base:'/yj/',
  routes: [
    {
      path: '/',
      component: App11
    },
    {
      path:'/bac',
      component:App1
    },
    {
      path:'/m',
      component:App3
    }
  ]
})



